.content {
  padding: 32px 18px 16px 18px;
  text-align: center;
  text-transform: uppercase;
  width: 284px;
  margin: 0 auto;

  @media (min-width: @tablet-width) {
    width: 520px;
  }

  @media (min-width: @desktop-width) {
    width: 484px;
  }
}

.content__slogan {
  margin: 0;
  padding: 0;
  margin-bottom: 35px;
}

.content__intro {
  margin: 0;
  padding: 0;
}
